@import url(https://fonts.googleapis.com/css?family=Fira+Code&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.search-container {
	height: 100%;
}

.tracks-container {
	max-height: 95%;
	overflow-y: auto;
}

.queue-container {
	min-height: 30%;
	max-height: 50%;
}

.queue-song-container {
	overflow-y: scroll;
}

.user-container {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
}

.song-container {
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: center;
	color: #DB504A;
}

.song-container:hover {
	background-color: #DB504A;
	color: #392F4B;
}

.info-container {
	overflow: hidden;
}

.info {
	font-size: 1.25em;
}

.text-purple {
	color: #392F4B;
}

.text-red {
	color: #DB504A;
}

.bg-purple {
	background-color: #392F4B;
}

.bg-red {
	background-color: #DB504A
}

.heading {
	font-size: 2.5em;
}

.action-box {
	width: 40vw;
	height: 60vh;
	padding: 1%;
}

.divider {
	width: 25%;
	background-color: white;
}

.custom-form-control, .custom-form-control:focus {
	background-color: #392F4B;
	border: 0;
}

.nav-item {
	color: white;
	background-color: transparent;
}

.nav-item:hover {
	text-decoration: none;
	color: #392F4B;
}

.dashboard-button {
	background-color: #272727;
	color: white;
}

.dashboard-button:hover {
	background-color: #DB504A;
	color: white;
}

.loading {
  -webkit-animation: spin infinite 5s linear;
          animation: spin infinite 5s linear;
  height: 50vh;
}

.room-code {
	font-family: 'Fira Code', monospace;
}

.add-button {
	color: #DB504A;
}

.add-button:hover {
	text-shadow: 1px 1px gray;
}

.current-listeners-container {
	height: 50vh;
}

#logout-button {
	background-color: transparent;
	border-color: transparent;
	color: white;
	height: 100%;
}

@-webkit-keyframes spin {
  from {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  to {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}

@keyframes spin {
  from {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  to {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}

